.stage{padding-top:28px;padding-left:80px;padding-right:80px;width:50%}@media(max-width: 1247px){.stage{padding-left:40px;padding-right:130px}}@media(max-width: 959px){.stage{width:100%}}@media(max-width: 767px){.stage{padding-left:16px;padding-right:16px}}.stage__wrapper{display:flex;align-items:start}.stage__number{display:inline-block;padding:4px 4px;margin-right:20px;background-color:#fee040;font-size:16px}.stage__title{font-size:24px;font-weight:bold}@media(max-width: 1247px){.stage__title{font-size:20px}}.stage__text{margin:18px 0 32px 0;font-size:18px;line-height:1.4}@media(max-width: 1247px){.stage__text{margin-top:18px;margin-bottom:20px;font-size:14px}}.stage__photo{width:70px;height:100%;margin-right:20px;border-radius:50%;border:7px solid #fff;box-shadow:10px 0 26px 9px #cac9c9}.stage__quote{margin-bottom:21px;font-size:15px;color:#505050;opacity:.7}.stage__quote--italic{font-style:italic}.stage__quote--italic:before{content:"“"}.stage__quote--italic:after{content:"”"}